* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: hsl(216deg, 12%, 8%);
  color: hsl(0deg, 0%, 100%);
  height: 100vh;
  font-size: 0.9375rem;
  font-family: "Overpass", sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
}

.bg {
  position: absolute;
  inset: 0;
  border-radius: inherit;
  backface-visibility: hidden;
  background: linear-gradient(to bottom, #202937 0%, hsl(215deg, 26%, 13%));
}

.card {
  background-color: hsl(216deg, 12%, 8%);
  width: 90%;
  max-width: 300px;
  padding: 20px;
  border-radius: 20px;
  position: relative;
  height: 300px;
}

.front {
  transform: perspective(300px) rotateY(0deg);
  z-index: 1;
  opacity: 1;
  transition: transform 1s linear, opacity 1s linear;
}

.front--active {
  z-index: 0;
  opacity: 0;
  transform: perspective(300px) rotateY(180deg);
}

.face {
  padding: 30px;
}
.face__img {
  background-color: hsl(204deg, 29%, 18%);
  border-radius: 50%;
  width: 33px;
  height: 33px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.face__img--svg {
  width: 12px;
}

.description {
  margin-top: 20px;
}
.description__heading {
  font-size: 18px;
  font-weight: 400;
}
.description__text {
  margin-top: 8px;
  font-size: 12px;
  font-weight: 200;
  line-height: 1.5;
  color: hsl(217deg, 8%, 55%);
}

.options {
  margin-top: 20px;
  display: flex;
  justify-content: space-between;
}
.options__item {
  background-color: hsl(204deg, 29%, 18%);
  border-radius: 50%;
  width: 33px;
  height: 33px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 12px;
  padding: 12px;
  color: hsl(217deg, 8%, 55%);
  text-decoration: none;
  transition: background-color 0.3s linear, color 0.3s linear;
  cursor: pointer;
}
.options__item:hover {
  background-color: hsl(25deg, 97%, 53%);
  outline: none;
  color: hsl(0deg, 0%, 100%);
}
.options__input {
  margin-top: 20px;
  width: 100%;
  padding: 8px;
  border: none;
  border-radius: 20px;
  background-color: hsl(25deg, 97%, 53%);
  color: hsl(0deg, 0%, 100%);
  cursor: pointer;
  letter-spacing: 2px;
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 700;
  transition: background-color 0.3s linear, color 0.3s linear;
}
.options__input:hover {
  color: hsl(25deg, 97%, 53%);
  background-color: hsl(0deg, 0%, 100%);
}

.checkbox {
  display: none;
}
.checkbox:checked ~ .options__item {
  background-color: hsl(217deg, 8%, 55%);
  color: white;
}

.back {
  transform: perspective(300px) rotateY(180deg);
  opacity: 0;
  z-index: 0;
  transition: transform 1s linear, opacity 1s linear;
}

.back--active {
  z-index: 1;
  opacity: 1;
  transform: perspective(300px) rotateY(360deg);
}

.back-card {
  padding: 30px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.back-card__img {
  width: 110px;
}

.selected {
  margin-top: 20px;
}
.selected__text {
  font-size: 12px;
  background-color: hsl(204deg, 29%, 18%);
  color: hsl(26deg, 67%, 57%);
  padding: 3px 15px;
  border-radius: 15px;
  font-weight: 400;
}

.gratitude {
  margin-top: 20px;
}
.gratitude__heading {
  font-size: 18px;
  font-weight: 400;
}
.gratitude__text {
  margin-top: 10px;
  font-size: 11.5px;
  color: hsl(217deg, 8%, 55%);
  line-height: 1.6;
}